<template>
  <div class="panel-tab__content">
    <div class="panel-tab__content--title">
      <span><i class="el-icon-menu" style="margin-right: 8px; color: #555555"></i></span>
      <el-button size="mini" type="primary" icon="el-icon-plus" @click="openModel('message')"></el-button>
    </div>
    <el-table :data="messageList" size="mini" border>
      <el-table-column type="index" label="" width="60px" />
      <el-table-column label="ID" prop="id" max-width="300px" show-overflow-tooltip />
      <el-table-column label="" prop="name" max-width="300px" show-overflow-tooltip />
    </el-table>
    <div class="panel-tab__content--title" style="padding-top: 8px; margin-top: 8px; border-top: 1px solid #eeeeee">
      <span><i class="el-icon-menu" style="margin-right: 8px; color: #555555"></i></span>
      <el-button size="mini" type="primary" icon="el-icon-plus" @click="openModel('signal')"></el-button>
    </div>
    <el-table :data="signalList" size="mini" border>
      <el-table-column type="index" label="" width="60px" />
      <el-table-column label="ID" prop="id" max-width="300px" show-overflow-tooltip />
      <el-table-column label="" prop="name" max-width="300px" show-overflow-tooltip />
      <el-table-column label="" prop="scope" max-width="300px" show-overflow-tooltip />
    </el-table>

    <el-dialog :visible.sync="modelVisible" :title="modelConfig.title" :close-on-click-modal="false" width="400px" append-to-body destroy-on-close>
      <el-form :model="modelObjectForm" size="mini" label-width="90px" @submit.native.prevent>
        <el-form-item :label="modelConfig.idLabel">
          <el-input v-model="modelObjectForm.id" clearable />
        </el-form-item>
        <el-form-item :label="modelConfig.nameLabel">
          <el-input v-model="modelObjectForm.name" clearable />
        </el-form-item>
        <el-form-item v-if="modelConfig.scopeLabel" :label="modelConfig.scopeLabel">
          <el-select v-model="modelObjectForm.scope" placeholder="">
            <el-option v-for="item in scope" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template slot="footer">
        <el-button size="mini" @click="modelVisible = false"> </el-button>
        <el-button size="mini" type="primary" @click="addNewObject"> </el-button>
      </template>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "SignalAndMassage",
  props: {
    force: Number
  },
  data() {
    return {
      signalList: [],
      messageList: [],
      modelVisible: false,
      modelType: "",
      modelObjectForm: {},
      scope: [
        {
          value: "processInstance",
          label: ""
        },
        {
          value: "global",
          label: ""
        }
      ]
    };
  },
  watch: {
    force: {
      immediate: false,
      handler: function(val) {
        if (val) {
          this.$nextTick(() => this.initDataList());
        }
      }
    }
  },
  computed: {
    modelConfig() {
      if (this.modelType === "message") {
        return { title: "", idLabel: "ID", nameLabel: "" };
      } else {
        return { title: "", idLabel: "ID", nameLabel: "", scopeLabel: "" };
      }
    }
  },
  mounted() {
    this.initDataList();
  },
  methods: {
    initDataList() {
      this.rootElements = window.bpmnInstances.modeler.getDefinitions().rootElements;
      this.messageIdMap = {};
      this.signalIdMap = {};
      this.messageList = [];
      this.signalList = [];
      this.rootElements.forEach(el => {
        if (el.$type === "bpmn:Message") {
          this.messageIdMap[el.id] = true;
          this.messageList.push({ ...el });
        }
        if (el.$type === "bpmn:Signal") {
          this.signalIdMap[el.id] = true;
          this.signalList.push({ ...el });
        }
      });
    },
    openModel(type) {
      this.modelType = type;
      this.modelObjectForm = {};
      this.modelVisible = true;
    },
    addNewObject() {
      if (this.modelType === "message") {
        if (this.messageIdMap[this.modelObjectForm.id]) {
          return this.$message.error("id");
        }
        const messageRef = window.bpmnInstances.moddle.create("bpmn:Message", this.modelObjectForm);
        this.rootElements.push(messageRef);
      } else {
        if (this.signalIdMap[this.modelObjectForm.id]) {
          return this.$message.error("id");
        }
        const signalRef = window.bpmnInstances.moddle.create("bpmn:Signal", this.modelObjectForm);
        this.rootElements.push(signalRef);
      }
      this.modelVisible = false;
      this.initDataList();
    }
  }
};
</script>
